import React, {Component} from 'react';
import {T, getSelection} from "./Utils";

class Confirm extends Component {
    constructor(props) {
        super(props)
        this.state = {
            messages: [],
        };
    }
    handleBuild = (e) => {
        setTimeout(() => {
            const items = ['ogra', 'ondra', 'alfonso', 'konrad']
            let item = items[Math.floor(Math.random()*items.length)];
            this.setState({messages: ['Waking trained chef... ' + item]})
        }, 1000)
        setTimeout(() => {
            let m = this.state.messages
            m.push('Cranking the build machine...')
            this.setState({messages: m})
        }, 2000)
        setTimeout(() => {
            let m = this.state.messages
            m.push('This is just a demo, but you get the idea')
            this.setState({messages: m})
        }, 3000)
    }

    renderSelected() {
        return (
            <div className="row">
                <ul className="p-list summary">
                    <li className="p-list__item is-ticked">{this.props.board.board.name}</li>
                    <li className="p-list__item is-ticked">Ubuntu {T(this.props.board.os.type)} {this.props.board.os.version}</li>
                    <li className="p-list__item is-ticked">{this.props.board.snaps.length !== 1 ? this.props.board.snaps.length + ' ' + T('snaps'): '1 '+ T('snap')}</li>
                </ul>
            </div>
        )
    }

    renderConsole() {
        if (this.state.messages.length === 0) {
            return
        }
        return (
            <pre className="console">
                {this.state.messages.map(m => {
                    return m + '\n'
                })}
            </pre>
        )
    }

    render() {
        let selected = this.props.board
        return (
            <div className="first">
                <h2>{T('confirm-configuration')}</h2>
                {this.renderSelected()}

                <div className="row">
                    <pre>
                        <code>
                            board:{'\n'}
                            {}  name: {selected.board.name}{'\n'}
                            {}  os:{'\n'}
                            {}    type: {selected.os.type}{'\n'}
                            {}    version: {selected.os.version}{'\n'}
                            {}  snaps:{'\n'}
                            {selected.snaps.map(s => {
                                return '    ' + s + '\n'
                            })}
                        </code>
                    </pre>
                </div>

                <div>
                    {T('ready-to-build')} &nbsp;
                    <button className="p-button--brand" onClick={this.handleBuild}>{T('build')}</button>
                </div>

                <div className="row">
                    {this.renderConsole()}
                </div>
            </div>
        );
    }
}

export default Confirm;